#root,.cus-sider{height:100%}.cus-sider{background:#001529;box-shadow:2px 0 6px rgba(0,21,41,.35)}.cus-sider .logo{background:#002140;height:64px;line-height:64px}.cus-sider .logo h1{white-space:nowrap;color:#fff;display:inline-block;vertical-align:middle;font-size:20px;margin-left:20px;font-family:Myriad Pro,Helvetica Neue,Arial,Helvetica,sans-serif;font-weight:600}.cus-sider .menu{background:#001529}.cus-sider .ant-menu-dark .ant-menu-inline.ant-menu-sub,.cus-sider .sub-menu{background:#000c17}.ant-layout-sider-children{position:fixed;transition:all .2s;width:250px}.ant-layout-sider-collapsed .ant-layout-sider-children{width:0}


